.location-searchbox {
    width: 85%;
    padding: 30px;
    margin: 60px auto;
    border-radius: 10px;
    background-color: #ffffff;
    box-shadow: 0 2px 16px 0 rgba(0,0,0,0.08);
    -webkit-transition: border-color 0.4s, background-color 0.4s, -webkit-box-shadow 0.4s;
    transition: border-color 0.4s, background-color 0.4s, box-shadow 0.4s;
    transition: border-color 0.4s, background-color 0.4s, box-shadow 0.4s, -webkit-box-shadow 0.4s;
    &:hover {
      box-shadow: 0px 30px 30px -20px rgba(0, 0, 0, 0.15);
    }
    .searchbox{
        width: 60%;
        border-width: 1px;
        border-radius: 4px;
        border-color: #cfd8e3;
        border-style: solid;
        color: #a5b6c9;
        background-color: #f8f9fd;
        padding: 12px 15px 12px 15px;
        &:focus{
            outline: 0;
            color: #495057;
            background-color: #fff;
        }
        &:hover{
            color: #495057;
        }
    }
    .current-location{
        display: inline-block;
        cursor: pointer;
        span{
            vertical-align: middle;
            padding: 0px 10px 0px 20px;
        }
        .current-location-icon{
            width: 25px;
            height: auto;
            vertical-align: middle;
            display: inline-block;
        }
    }

}
.map-container {
  width: 80%;
  height: 600px;
  margin: 50px auto;
}
